import Vue from 'vue'
import Router from 'vue-router'
import index from '@/components/index'
import graph from '@/components/graph'
Vue.use(Router)

export default new Router({
  mode: 'history',
  routes: [
    {
      path: '/',
      name: 'index',
      component: index
    },
    {
      path: '/graph',
      name: 'graph',
      component: graph
    }

  ]
})



