<template>
  <div class="mainBody">
    <div>
      <div class="header">
        <div class="leftStyle">
          <img src="../imgs/logo.png" />
        </div>
        <div v-if="recordSign">
          <img src="../imgs/video.png" class="chlickLogo" />
          <span class="chlickWord" @click="recordStart">开始录制</span>
        </div>
        <div v-else>
          <img src="../imgs/suspend.png" class="chlickLogo" />
          <span class="chlickWord" @click="recordEnd">结束录制</span>
        </div>
      </div>
      <img src="../imgs/circle.png" id="circleLogo" />
      <canvas id="canvas" width="1700" height="1001"></canvas>
      <div class="block">
        <block></block>
      </div>
      <div class="bigCamera">
        <bcamera></bcamera>
      </div>
      <div class="smallCamera">
        <camera></camera>
      </div>
      <div>
        <img :src="buttonUrl" class="switchStyle" @click="buttonEvent" />
      </div>
      <div class="tbImg" :style="{transform:'scale('+multiples+')',transformOrigin:'top left'}">
        <img
          src="../imgs/tb-2.png"
          :style="{transform:'rotateZ('+deg+'deg)', width: '376px', height: '376px'}"
          @click="rotate"
          v-if="showPicture"
        />
      </div>
      <div class="showImage1">
        <img
          src="../imgs/camera.jpg"
          @click="show(1)"
          v-if="showButton1"
          :style="{width: '50px', height: '50px'}"
        />
      </div>
      <div class="showImage2">
        <img
          src="../imgs/camera.jpg"
          @click="show(2)"
          v-if="showButton2"
          :style="{width: '50px', height: '50px'}"
        />
      </div>
    </div>
  </div>
</template>

<script>
import block from "./block";
import camera from "./camera";
import bcamera from "./bigCamera";
import index from "./index3D";
import logo from "../imgs/logo.png";
import axios from "axios";

export default {
  components: { block, bcamera, camera },
  data() {
    return {
      deg: 0,
      multiples: 1,
      showPicture: false,
      showButton1: true,
      showButton2: true,
      buttonState: 0,
      buttonUrl: require("../imgs/switch_off.png"),
      recordSign: true,
      endTime: "",
      // route1: [830, 260, 1430, 710],
      route1:{'2d_camera':{'x':830,'y':260}, '2d_pos':{'x':1430,'y':700}},
      route2: {'2d_camera':{'x':620,'y':160}, '2d_pos':{'x':1430,'y':490}},
      pos1: ["650px", "120px"],
      pos2: ["420px", "60px"],
      word: ""
    };
  },
  methods: {
    rotate() {
      this.deg += 1;
      if (this.deg >= 360) {
        this.deg = 0;
      }
    },
    draw(sign) {
      var canvas = document.getElementById("canvas");
      if (canvas.getContext) {
        var ctx = canvas.getContext("2d");
        ctx.strokeStyle = "#5CE2FF";
        ctx.lineWidth = 2;
        if (sign == 1) {
          var route_pos = this.route1;
        } else if (sign == 2) {
          var route_pos = this.route2;
        }
        ctx.beginPath();
        ctx.clearRect(0, 0, 1700, 1000);
        ctx.moveTo(route_pos['2d_camera']['x'], route_pos['2d_camera']['y']);
        ctx.lineTo(route_pos['2d_camera']['x'], route_pos['2d_pos']['y']);
        ctx.lineTo(route_pos['2d_pos']['x'], route_pos['2d_pos']['y']);
        ctx.stroke();
      }
    },
    show(data) {
      this.showPicture = true;
      if (data == 1) {
        this.showButton1 = false;
        this.showButton2 = true;
      } else if (data == 2) {
        this.showButton2 = false;
        this.showButton1 = true;
      }
      this.change(data);
      this.draw(data);
    },
    change(data) {
      let obj = document.getElementsByClassName("tbImg")[0];
      if (data == 1) {
        var pos_data = this.pos1;
      } else if (data == 2) {
        var pos_data = this.pos2;
      }
      obj.style.left = pos_data[0];
      obj.style.top = pos_data[1];
    },
    buttonEvent() {
      if (this.buttonState === 0) {
        this.buttonUrl = require("../imgs/switch_on.png");
        this.buttonState = 1;
      } else {
        this.buttonUrl = require("../imgs/switch_off.png");
        this.buttonState = 0;
      }
      axios({
        method: "post",
        url: process.env.API_ROOT + "/main/api/state",
        data: { state: this.buttonState, drivers: "switch" }
      }).then(res => {
        console.log(res);
      });
    },
    recordStart() {
      this.recordSign = false;
      this.open("<h3>&nbsp;&nbsp;&nbsp;开始录制</h3>");
      this.saveCameraEvent("开始录制");
      this.imgShowOff();
    },
    recordEnd() {
      this.recordSign = true;
      this.open("<h3>&nbsp;&nbsp;&nbsp;结束录制</h3>");
      this.saveCameraEvent("结束录制");
      this.imgShowOff();
    },
    saveCameraEvent(word) {
      this.endTime = this.currentTime();
      axios({
        method: "post",
        url: process.env.API_ROOT + "/main/api/event",
        data: { event_time: this.endTime, state: word }
      }).then(res => {
        console.log(res);
      });
    },
    open(msg) {
      this.$message({
        dangerouslyUseHTMLString: true,
        message: msg,
        type: "error",
        iconClass: "el-icon-video-camera-solid"
      });
    },
    currentTime() {
      let date = new Date();
      let y = date.getFullYear();
      let m = date.getMonth() + 1;
      let d = date.getDate();
      let H = date.getHours();
      let mm = date.getMinutes();
      let s = date.getSeconds();
      m = m < 10 ? "0" + m : m;
      d = d < 10 ? "0" + d : d;
      H = H < 10 ? "0" + H : H;
      mm = mm < 10 ? "0" + mm : mm;
      s = s < 10 ? "0" + s : s;
      return y + "-" + m + "-" + d + " " + H + ":" + mm + ":" + s;
    },
    getState() {
      axios({
        method: "get",
        url: process.env.API_ROOT + "/main/api/event"
      }).then(res => {
        if (res.data.state == "结束录制") {
          this.recordSign = true;
        } else {
          this.recordSign = false;
        }
        this.imgShowOff();
      });
    },
    imgShowOff() {
      console.log(this.recordSign);
      if (this.recordSign == false) {
        var imgBox = document.getElementById("circleLogo");
        imgBox.style.display = "block";
        imgBox.style.animation = "flashCirle 1s infinite";
      } else {
        var imgBox = document.getElementById("circleLogo");
        imgBox.style.animation = "";
        imgBox.style.display = "none";
      }
    }
  },
  created() {
    setInterval(this.rotate, 10);
    console.log(process.env.NODE_ENV);
  },
  mounted() {
    // this.draw(1)
    this.getState();
  }
};
</script>

<style scoped>
.header {
  margin: 0;
  height: 70px;
  border-bottom: 1px solid #5ce2ff;
}
.leftStyle {
  float: left;
}
.tbImg {
  position: absolute;
  left: 557px;
  top: 438px;
}
.block {
  position: absolute;
  left: 35px;
  top: 30px;
}

.mainBody{
  background: url(../imgs/timg.jpg);
  background-size: cover;
  background-position: center;
  height: 1080px;
  width: 100%;
}
.bigCamera {
  position: absolute;
  left: 72%;
  top: 100px;
}
.smallCamera {
  position: absolute;
  left: 72%;
  top: 550px;
}

::-webkit-scrollbar {
  width: 0 !important;
}
::-webkit-scrollbar {
  width: 0 !important;
  height: 0;
}
.chlickWord {
  color: white;
  position: absolute;
  left: 1825px;
  top: 23px;
}
.chlickLogo {
  position: absolute;
  left: 1780px;
  top: 10px;
  z-index: 300;
  width: 35px;
}
#circleLogo {
  position: absolute;
  left: 1740px;
  top: 10px;
  display: none;
}
@keyframes flashCirle {
  0% {
    opacity: 1;
  }
  50% {
    opacity: 0;
  }
}
@keyframes Cirle {
  0% {
    opacity: 0;
  }
  50% {
    opacity: 0;
  }
}
#canvas {
  position: absolute;
}
.showImage1 {
  position: absolute;
  z-index: 399;
  left: 800px;
  top: 300px;
}
.showImage2 {
  position: absolute;
  z-index: 399;
  left: 600px;
  top: 200px;
}
.switchStyle {
  position: relative;
  z-index: 300;
  width: 100px;
  left: 600px;
  top: -400px;
}
.el-message {
  background-color: #fef0f0;
  border-color: #fde2e2;
  color: #f56c6c;
}
</style>


