<template>
    <div class="small-camera">
        <div class="leftImage">
            <div class="left-block">
                <img src="../imgs/board-top.png" class="board-camera1-small" v-if="camera1"/>
                <span class="small-title-1">摄像头</span>
                <el-image :src="src1" class="left-cameras" @click="choice(1)"></el-image>
                <img src="../imgs/board-word.png" class="board-camera1-word">
                <img src="../imgs/board-word.png" class="board-camera1-bottom">
                <span class="small-name-1">摄像头1</span>
                <span class="small-state-1">待机</span>
                <img src="../imgs/board-bottom.png" class="board-camera1-bottom" v-if="camera1"/>
            </div>
            <div class="right-block">
                <img src="../imgs/board-top.png" class="board-camera2-small" v-if="camera2"/>
                <el-image :src="src2" class="left-cameras" @click="choice(2)"></el-image>
                <span class="small-title-1">摄像头</span>
                <img src="../imgs/board-word.png" class="board-camera2-word">
                <img src="../imgs/board-word.png" class="board-camera1-bottom">
                <span class="small-name-1">摄像头2</span>
                <span class="small-state-1">待机</span>
                <img src="../imgs/board-bottom.png" class="board-camera1-bottom" v-if="camera2"/>
            </div>
        </div>
        <div class="rightImage">
            <div class="left-block">
                <img src="../imgs/board-word.png" class="board-camera3-word">
                <img src="../imgs/board-top.png" class="board-camera3-small" v-if="camera3"/>
                <el-image :src="src1" class="right-cameras" @click="choice(3)"></el-image>
                        <span class="small-title-2">摄像头</span>
                <img src="../imgs/board-word.png" class="board-camera2-bottom">
                <span class="small-name-2">摄像头3</span>
                <span class="small-state-2">待机</span>
                <img src="../imgs/board-bottom.png" class="board-camera2-bottom" v-if="camera3"/>

            </div>
            <div class="right-block">
                <img src="../imgs/board-word.png" class="board-camera3-word">
                <img src="../imgs/board-top.png" class="board-camera3-small" v-if="camera4"/>
                <el-image :src="src1" class="right-cameras" @click="choice(4)"></el-image>
                <span class="small-title-2">摄像头</span>
                <img src="../imgs/board-word.png" class="board-camera2-bottom">
                <span class="small-name-2">摄像头4</span>
                <span class="small-state-2">待机</span>
                <img src="../imgs/board-bottom.png" class="board-camera2-bottom" v-if="camera4"/>
            </div>
        </div>
    </div>
</template>

<script>


export default {
    data() {
        return {
            src1:'http://192.168.3.88:8090/camera1.mjpg',
            src2:'http://192.168.3.88:8090/camera2.mjpg',
            camera1:false,
            camera2:false,
            camera3:false,
            camera4:false,
        }
    },
    methods:{
        choice(num){
            if (num==1) {
                this.camera1 = true
                this.camera2 = false
                this.camera3 = false
                this.camera4 = false
            } else if (num==2) {
                this.camera1 = false
                this.camera2 = true
                this.camera3 = false
                this.camera4 = false
            } else if (num==3) {
                this.camera1 = false
                this.camera2 = false
                this.camera3 = true
                this.camera4 = false
            } else if (num==4) {
                this.camera1 = false
                this.camera2 = false
                this.camera3 = false
                this.camera4 = true
            }
        }
    },
}
</script>

<style scoped>
    .left-block{
        float: left;
        width: 50%;
        height: 50%;
        position: relative;
    }
    .right-block{
        float: left;
        width: 50%;
        height: 50%;
        position: relative;
    }
    .small-camera{
        width: 520px;
    }
    .board-camera1-small{
        width: 255px;
        height: 20px;
        position: absolute;
        left: 0;
        top:20px;
        z-index: 99;
    }
    .board-camera2-small{
        width: 255px;
        height: 20px;
        position: absolute;
        top:20px;
        z-index: 99;
    }
    .board-camera3-small{
        width: 255px;
        height: 20px;
        position: absolute;
        left: 0;
        top:40px;
        z-index: 99;
    }
    .board-camera3-word{
        position: absolute;
        left: 0px;
        top: 20px;
        height:20px;
        width: 255px;
        z-index: 98;
    }

    .left-cameras{
        width: 255px;
        height: 190px;
        top: 20px;
    }
    .right-cameras{
        width: 255px;
        height: 190px;
        top: 40px;
    }
    .board-camera1-word{
        position: absolute;
        left: 0px;
        top: 0px;
        height:20px;
        width: 255px;
        z-index: 98;
    }
    .board-camera2-word{
        position: absolute;
        top: 0px;
        left: 0px;
        height:20px;
        width: 255px;
        z-index: 98;
    }
    .board-camera1-bottom{
        position: absolute;
        top: 190px;
        left: 0px;
        height:20px;
        width: 255px;
        z-index: 98;
    }
    .board-camera2-bottom{
        position: absolute;
        top: 210px;
        left: 0px;
        height:20px;
        width: 255px;
        z-index: 98;
    }
    .small-title-1{
        position: absolute;
        color: #ffffff;
        left: 100px;
        z-index: 100;
    }
    .small-title-2{
        position: absolute;
        color: #ffffff;
        left: 100px;
        top: 20px;
        z-index: 100;
    }
    .small-name-1{
        position: absolute;
        color: #ffffff;
        top: 190px;
        left: 5px;
        z-index: 100;
    }
    .small-state-1{
        position: absolute;
        color: #ffffff;
        top: 190px;
        left: 220px;
        z-index: 100;
    }
    .small-name-2{
        position: absolute;
        color: #ffffff;
        top: 210px;
        left: 5px;
        z-index: 100;
    }
    .small-state-2{
        position: absolute;
        color: #ffffff;
        top: 210px;
        left: 220px;
        z-index: 100;
    }
</style>


