<template>
  <div class="block">
    <div class="black-one">
      <span class="title">传感器</span>
      <img src="../imgs/board-top.png" class="board-top" />
      <img src="../imgs/board-word.png" class="board-word-first" />
    </div>
    <div class="sensor-table">
      <template>
        <el-table :data="sensorData" style="width: 100%">
          <el-table-column prop="friendly_name" label="设备" width="110px"></el-table-column>
          <el-table-column prop="state" label="状态" width="100px" align="center">
            <template slot-scope="scope">
              <el-button :type="scope.row.state === '触发' ? '' : 'primary'">{{ scope.row.state}}</el-button>
            </template>
          </el-table-column>
          <el-table-column prop="other" label="其他" align="right" width="110px"></el-table-column>
        </el-table>
      </template>
    </div>

    <div class="block-two">
      <div>
        <span class="title">网关设备</span>
        <img src="../imgs/board-word.png" class="board-word-first" />
        <div class="sensor-table">
          <template>
            <el-table :data="trackerData" style="width: 100%">
              <el-table-column prop="friendly_name" label="设备" width="110px"></el-table-column>
              <el-table-column prop="state" label="状态" align="center" width="100px">
                <template slot-scope="scope">
                  <el-button :type="scope.row.state === '在线' ? 'primary' : ''">{{ scope.row.state}}</el-button>
                </template>
              </el-table-column>
              <el-table-column prop="other" label="其他" align="right" width="110px"></el-table-column>
            </el-table>
          </template>
        </div>
      </div>
    </div>

    <div class="block-three">
      <div>
        <span class="title">群晖</span>
        <img src="../imgs/board-word.png" class="board-word-first" />
        <div class="sensor-table">
          <template>
            <el-table :data="synologyData" style="width: 100%">
              <el-table-column prop="friendly_name" label="设备" width="110px"></el-table-column>
              <el-table-column prop="state" label="状态" width="100px" align="center">
                <template slot-scope="scope">
                  <el-button type="primary">{{ scope.row.state}}</el-button>
                </template>
              </el-table-column>
              <el-table-column prop="other" label="其他" align="right" width="110px"></el-table-column>
            </el-table>
          </template>
        </div>
      </div>
    </div>
    <img src="../imgs/board-bottom.png" class="board-bottom" />
  </div>
</template>



<script>
import axios from "axios";

export default {
  data() {
    return {
      tableData: [],
      synologyData: [],
      sensorData: [],
      trackerData: []
    };
  },
  methods: {
    getData() {
      axios({
        method: "get",
        url: process.env.API_ROOT + "/main/api/state"
      }).then(res => {
        this.synologyData = res.data.Synology;
        this.sensorData = res.data.Sensor;
        this.trackerData = res.data.Tracker;
      });
    }
  },
  created() {
    setInterval(this.getData, 1000);
  }
};
</script>



<style scoped>
.block {
  width: 400px;
  height: 800px;
  position: relative;
  overflow-x: auto;
}
.board-top {
  position: absolute;
  left: 0px;
  top: 0px;
  z-index: 100;
}
.board-word-first {
  position: absolute;
  left: 0px;
  top: 0px;
  z-index: 99;
}
.title {
  position: absolute;
  color: #ffffff;
  z-index: 200;
  left: 150px;
  top: 10px;
}
.el-table,
.el-table__expanded-cell {
  background-color: transparent !important;
  color: #ffffff;
}

.el-table th,
.el-table tr,
.el-table td {
  background-color: transparent !important;
}
.el-table__row > td {
  border: none;
  padding: 0px;
}
.el-table::before {
  height: 0px;
}
.el-table td,
.el-table th.is-leaf {
  border-bottom: none;
  color: #ffffff;
}
.sensor-table {
  position: relative;
  top: 40px;
}

.board-word-second {
  position: absolute;
  top: 230px;
  z-index: 99;
}
.block-two {
  position: relative;
  top: 80px;
}
.board-bottom {
  position: relative;
  top: 200px;
}
.el-button {
  border-radius: 15px;
  color: #096a7f;
  border-color: #687480;
  padding: 5px;
  width: 80px;
}
.el-button--primary {
  background-color: #5ce2ff;
}
.block-three {
  position: relative;
  top: 180px;
}
</style>



